/*--------------------------------------------------------------
# Typography
--------------------------------------------------------------*/
html,
body,
button,
input,
select,
optgroup,
textarea
{
    font-family: 'Noto Sans JP';
    font-size: 15px;
    line-height: 1.8; 

    color: #232323;
}
@media only screen and (min-width: 768px)
{
    html,
    body,
    button,
    input,
    select,
    optgroup,
    textarea
    {
        font-size: 16px;
    }
}

h1,
h2,
h3,
h4,
h5,
h6
{
    clear: both;
}

p
{
    margin-bottom: 1.5em;
}

dfn,
cite,
em,
i
{
    font-style: italic;
}

blockquote
{
    margin: 0 1.5em;
}

address
{
    margin: 0 0 1.5em;
}

pre
{
    font-family: 'Courier 10 Pitch', Courier, monospace;
    font-size: 14.0625px;
    line-height: 1.6;

    overflow: auto;

    max-width: 100%;
    margin-bottom: 1.6em;
    padding: 1.6em; 

    background: #eee;
}
@media only screen and (min-width: 768px)
{
    pre
    {
        font-size: 15px;
    }
}

code,
kbd,
tt,
var
{
    font-family: Monaco, Consolas, 'Andale Mono', 'DejaVu Sans Mono', monospace;
    font-size: 14.0625px;
}
@media only screen and (min-width: 768px)
{
    code,
    kbd,
    tt,
    var
    {
        font-size: 15px;
    }
}

abbr,
acronym
{
    cursor: help; 

    border-bottom: 1px dotted #666;
}

mark,
ins
{
    text-decoration: none; 

    background: #fff9c0;
}

big
{
    font-size: 125%;
}

/*--------------------------------------------------------------
# Elements
--------------------------------------------------------------*/
html
{
    box-sizing: border-box;
}

*,
*:before,
*:after
{
    /* Inherit box-sizing to make it easier to change the property for components that leverage other behavior; see https://css-tricks.com/inheriting-box-sizing-probably-slightly-better-best-practice/ */
    box-sizing: inherit;
}

body
{
    background: #fff;
    /* Fallback for when there is no custom background color defined. */
}

hr
{
    height: 1px;
    margin-bottom: 1.5em; 

    border: 0;
    background-color: #ccc;
}

ul,
ol
{
    margin: 0 0 1.5em 3em;
}

ul
{
    list-style: disc;
}

ol
{
    list-style: decimal;
}

li > ul,
li > ol
{
    margin-bottom: 0;
    margin-left: 1.5em;
}

dt
{
    font-weight: bold;
}

dd
{
    margin: 0 1.5em 1.5em;
}

img
{
    /* Make sure images are scaled correctly. */
    max-width: 100%;
    height: auto;
    /* Adhere to container width. */
}

figure
{
    margin: 1em 0;
    /* Extra wide images within figure tags don't overflow the content area. */
}

table
{
    width: 100%; 
    margin: 0 0 1.5em;
}

/*--------------------------------------------------------------
# Forms
--------------------------------------------------------------*/
button,
input[type='button'],
input[type='reset'],
input[type='submit']
{
    font-size: 11.25px;
    line-height: 1;

    padding: .6em 1em .4em; 

    color: rgba(0, 0, 0, .8);
    border: 1px solid;
    border-color: #ccc #ccc #bbb;
    border-radius: 3px;
    background: #e6e6e6;
}
@media only screen and (min-width: 768px)
{
    button,
    input[type='button'],
    input[type='reset'],
    input[type='submit']
    {
        font-size: 12px;
    }
}
button:hover,
input[type='button']:hover,
input[type='reset']:hover,
input[type='submit']:hover
{
    border-color: #ccc #bbb #aaa;
}
button:active,
button:focus,
input[type='button']:active,
input[type='button']:focus,
input[type='reset']:active,
input[type='reset']:focus,
input[type='submit']:active,
input[type='submit']:focus
{
    border-color: #aaa #bbb #bbb;
}

input[type='text'],
input[type='email'],
input[type='url'],
input[type='password'],
input[type='search'],
input[type='number'],
input[type='tel'],
input[type='range'],
input[type='date'],
input[type='month'],
input[type='week'],
input[type='time'],
input[type='datetime'],
input[type='datetime-local'],
input[type='color'],
textarea
{
    padding: 3px; 

    color: #666;
    border: 1px solid #ccc;
    border-radius: 3px;
}
input[type='text']:focus,
input[type='email']:focus,
input[type='url']:focus,
input[type='password']:focus,
input[type='search']:focus,
input[type='number']:focus,
input[type='tel']:focus,
input[type='range']:focus,
input[type='date']:focus,
input[type='month']:focus,
input[type='week']:focus,
input[type='time']:focus,
input[type='datetime']:focus,
input[type='datetime-local']:focus,
input[type='color']:focus,
textarea:focus
{
    color: #111;
}

select
{
    border: 1px solid #ccc;
}

textarea
{
    width: 100%;
}

/*--------------------------------------------------------------
# Navigation
--------------------------------------------------------------*/
/*--------------------------------------------------------------
## Links
--------------------------------------------------------------*/
a
{
    text-decoration: none; 

    color: #232323;
}
a:visited
{
    color: #232323;
}
a:hover,
a:focus,
a:active
{
    color: #232323;
}
a:focus
{
    outline: thin dotted;
}
a:hover,
a:active
{
    outline: 0;
}

/*--------------------------------------------------------------
## Menus
--------------------------------------------------------------*/
.main-navigation
{
    display: block;
    float: left;
    clear: both;

    width: 100%;
}
.main-navigation ul
{
    display: none;

    margin: 0;
    padding-left: 0; 

    list-style: none;
}
.main-navigation ul ul
{
    position: absolute;
    z-index: 99999; 
    top: 100%;
    left: -999em;

    float: left;

    box-shadow: 0 3px 3px rgba(0, 0, 0, .2);
}
.main-navigation ul ul ul
{
    top: 0; 
    left: -999em;
}
.main-navigation ul ul li:hover > ul,
.main-navigation ul ul li.focus > ul
{
    left: 100%;
}
.main-navigation ul ul a
{
    width: 200px;
}
.main-navigation ul li:hover > ul,
.main-navigation ul li.focus > ul
{
    left: auto;
}
.main-navigation li
{
    position: relative; 

    float: left;
}
.main-navigation a
{
    display: block;

    text-decoration: none;
}

/* Small menu. */
.menu-toggle,
.main-navigation.toggled ul
{
    display: block;
}

@media screen and (min-width: 37.5em)
{
    .menu-toggle
    {
        display: none;
    }
    .main-navigation ul
    {
        display: block;
    }
}

.site-main .comment-navigation,
.site-main
.posts-navigation,
.site-main
.post-navigation
{
    overflow: hidden; 

    margin: 0 0 1.5em;
}

.comment-navigation .nav-previous,
.posts-navigation .nav-previous,
.post-navigation .nav-previous
{
    float: left;

    width: 50%;
}

.comment-navigation .nav-next,
.posts-navigation .nav-next,
.post-navigation .nav-next
{
    float: right;

    width: 50%; 

    text-align: right;
}

/*--------------------------------------------------------------
# Accessibility
--------------------------------------------------------------*/
/* Text meant only for screen readers. */
.screen-reader-text
{
    position: absolute !important;

    overflow: hidden;
    clip: rect(1px, 1px, 1px, 1px);

    width: 1px;
    height: 1px;
    margin: -1px;
    padding: 0;

    word-wrap: normal !important;

    border: 0;

    clip-path: inset(50%);
    /* Many screen reader and browser combinations announce broken words as they would appear visually. */
}
.screen-reader-text:focus
{
    font-size: 13.125px;
    font-weight: bold;
    line-height: normal;

    z-index: 100000;
    top: 5px;
    left: 5px;

    display: block;
    clip: auto !important;

    width: auto;
    height: auto;
    padding: 15px 23px 14px;

    text-decoration: none;

    color: #21759b;
    border-radius: 3px;
    background-color: #f1f1f1;
    box-shadow: 0 0 2px 2px rgba(0, 0, 0, .6);

    clip-path: none;
    /* Above WP toolbar. */
}
@media only screen and (min-width: 768px)
{
    .screen-reader-text:focus
    {
        font-size: 14px;
    }
}

/* Do not show the outline on the skip link target. */
#content[tabindex='-1']:focus
{
    outline: 0;
}

/*--------------------------------------------------------------
# Alignments
--------------------------------------------------------------*/
.alignleft
{
    display: inline;
    float: left;

    margin-right: 1.5em;
}

.alignright
{
    display: inline;
    float: right;

    margin-left: 1.5em;
}

.aligncenter
{
    display: block;
    clear: both;

    margin-right: auto; 
    margin-left: auto;
}

/*--------------------------------------------------------------
# Clearings
--------------------------------------------------------------*/
.clear:before,
.clear:after,
.entry-content:before,
.entry-content:after,
.comment-content:before,
.comment-content:after,
.site-header:before,
.site-header:after,
.site-content:before,
.site-content:after,
.site-footer:before,
.site-footer:after
{
    display: table;
    clear: both; 

    table-layout: fixed;

    content: '';
}

.clear:after,
.entry-content:after,
.comment-content:after,
.site-header:after,
.site-content:after,
.site-footer:after
{
    clear: both;
}

/*--------------------------------------------------------------
# Widgets
--------------------------------------------------------------*/
.widget
{
    margin: 0 0 1.5em;
    /* Make sure select elements fit in widgets. */
}
.widget select
{
    max-width: 100%;
}

/*--------------------------------------------------------------
# Content
--------------------------------------------------------------*/
.site-content
{
    padding-top: 78px;
    padding-bottom: 60px;
}
@media only screen and (min-width: 768px)
{
    .site-content
    {
        padding-top: 82px;
    }
}

.content-area
{
    float: none;

    width: 100%;
    margin: 0;
}
@media only screen and (min-width: 768px)
{
    .content-area
    {
        float: left;

        width: 100%; 
        margin: 0 -25% 0 0;
    }
}

.site-main
{
    margin: 0;
}
@media only screen and (min-width: 768px)
{
    .site-main
    {
        margin: 0 25% 0 0;
    }
}

.site-content .widget-area
{
    float: none;

    width: 100%;
    margin: 0;
}
@media only screen and (min-width: 768px)
{
    .site-content .widget-area
    {
        float: right;
        overflow: hidden;

        width: 25%;
    }
}

.site-footer
{
    clear: both;

    width: 100%;
}

.no-sidebar .content-area
{
    float: none;

    margin-right: auto; 
    margin-left: auto;
}

.no-sidebar .site-main
{
    margin-right: 0;
}

/*--------------------------------------------------------------
## Posts and pages
--------------------------------------------------------------*/
.sticky
{
    display: block;
}

.post,
.page
{
    margin: 0 0 1.5em;
}

.updated:not(.published)
{
    display: none;
}

.page-content,
.entry-content,
.entry-summary
{
    margin: 1.5em 0 0;
}

.page-links
{
    clear: both;

    margin: 0 0 1.5em;
}

/*--------------------------------------------------------------
## Comments
--------------------------------------------------------------*/
.comment-content a
{
    word-wrap: break-word;
}

.bypostauthor
{
    display: block;
}

/*--------------------------------------------------------------
## Layout
--------------------------------------------------------------*/
.l-header
{
    position: fixed;
    z-index: 10000;
}

.l-footer
{
    padding: 30px 0; 

    text-align: center;

    color: #fff;
    background-color: #232323;
}
@media only screen and (min-width: 768px)
{
    .l-footer
    {
        padding: 140px 0 20px;
    }
}
.l-footer__logo
{
    font-family: 'Anton';
    font-size: 2rem;

    margin-top: 0;
    margin-bottom: 10px; 
    padding: 0 0 10px;

    text-indent: 6px;
    letter-spacing: 6px;
}
@media only screen and (min-width: 768px)
{
    .l-footer__logo
    {
        margin-bottom: 40px; 

        letter-spacing: 16px;
    }
}
.l-footer__copyright
{
    font-size: .8rem;

    margin: 6px auto;
}
.l-footer a
{
    color: #fff;
}

/*--------------------------------------------------------------
## Object
--------------------------------------------------------------*/
/*--- #Component  ---*/
.c-logo
{
    font-family: 'Anton';
    font-size: .8rem;
    font-weight: bold; 

    letter-spacing: .9rem;
}
.c-logo h1
{
    line-height: 1;

    padding-top: 6px;
}

.c-contactLink
{
    font-size: .7rem;
    line-height: 1;

    display: inline-block;

    padding: 8px 30px;

    text-decoration: none;
    letter-spacing: 6px; 

    color: #232323;
    border: 2px solid #232323;
}

.c-slideMenuButton
{
    position: absolute;
    top: 32px;
    right: 16px; 

    display: inline-block;
}
@media only screen and (min-width: 768px)
{
    .c-slideMenuButton
    {
        position: static;
    }
}
.c-slideMenuButton__wrap
{
    position: relative; 

    width: 40px;
    height: 16px;
}
@media only screen and (min-width: 768px)
{
    .c-slideMenuButton__wrap
    {
        width: 56px;
        height: 18px;
    }
}
.c-slideMenuButton__wrap span
{
    position: absolute;

    display: inline-block;

    width: 100%;
    height: 2px;

    transition: .3s all; 

    background-color: #232323;
}
.c-slideMenuButton__wrap span:nth-of-type(1)
{
    top: 0;
    left: 0;
}
.c-slideMenuButton__wrap span:nth-of-type(2)
{
    bottom: 0;
    left: 0;
}
.c-slideMenuButton.c-slideMenuButton.open .c-slideMenuButton__wrap span:nth-of-type(1)
{
    top: 8px; 

    transform: rotate(45deg);
}
.c-slideMenuButton.c-slideMenuButton.open .c-slideMenuButton__wrap span:nth-of-type(2)
{
    top: 8px; 

    transform: rotate(-45deg);
}

.c-thumbnailMenu
{
    display: block;

    text-align: center; 

    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
}
.c-thumbnailMenu--service
{
    background-image: url(./images/our_service.jpg);
}
.c-thumbnailMenu--design
{
    background-image: url(./images/about_us.jpg);
}
.c-thumbnailMenu__bgc
{
    padding: 100px 0 80px;
}
@media only screen and (min-width: 768px)
{
    .c-thumbnailMenu__bgc
    {
        padding: 200px 0;
    }
}
.c-thumbnailMenu--service .c-thumbnailMenu__bgc
{
    background-color: rgba(0, 0, 0, .6);
}
.c-thumbnailMenu--design .c-thumbnailMenu__bgc
{
    background-color: rgba(0, 0, 0, .4);
}
.c-thumbnailMenu__bgc .c-thumbnailMenu__title
{
    font-size: 1rem;
    line-height: 1.8; 

    margin: 8px auto;

    color: #fff;
}
@media only screen and (min-width: 768px)
{
    .c-thumbnailMenu__bgc .c-thumbnailMenu__title
    {
        font-size: 1.3rem;
        font-weight: bold;
