
$class : '.p-drawerMenu ';

#{$class} {
  position: fixed;
  top:0;
  right:-70%;
  width: 70%;
  height: 100%;
  color: #fff;
  background-color: $color__site-main;
  transition: 0.5s;
  padding: 0 20px;
  @include mq-md {
    right:-30%;
    width: 30%;
  }
  &.open {
    right:0%;
  }

  &__logo {
    h2 {
      padding: 0;
      font-size: 2rem;
    }
  }

  a,
  a:visited,
	a:hover,
	a:focus,
	a:active {
		color: #fff;
	}

  ul {
    padding: 0;
    margin: 0;
    list-style: none;

    li {
      margin-bottom: 10px;
      letter-spacing: 2px;
      font-weight: bold;
      &::before {
        font-family:"FontAwesome";
        content: '\f054';
        display: inline;
        margin-right: 4px;
      }
    }
  }
}

#slideMenuOut {
  transition: 0.5s all;
  &.open {
    transform:translateX(-70%);
    position: fixed;
    @include mq-md {
      transform:translateX(-30%);
    }
  }
}

#slideMenuOverlay {
  width: 100%;
  height: 100%;
  display: none;
  position: fixed;
  top:0;
  right:0;
  &.open {
    display: block;
  }
}
